.header {
  margin: 0;
  width: 100%;
  position: relative;
}
.menu {
  position: fixed;
  width: 45px;
  height: 45px;
  line-height: 45px;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  color: #fff;
  right: 20px;
  top: 20px;
  background-color: rgb(161, 151, 151);
  display: none;
  z-index: 2;
}
.list_nav {
  display: flex;
  top: 0;
  left: 0;
  gap: 18px;
  list-style: none;
  font-size: 14px;
  padding: 40px;
  background-color: rgb(0, 0, 0);
  color: aliceblue;
  text-transform: uppercase;
  margin: 0 0 40px 0;
  a {
    text-decoration: none;
    font-weight: 600;
    &:visited {
      color: #ccc;
    }
  }
}

@media screen and (max-width: 1200px) {
  .list_nav {
    font-size: 13px;
    padding: 38px;
    margin: 0 0 35px 0;
  }
}
@media screen and (max-width: 950px) {
  .list_nav {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100vh;
    gap: 18px;
    font-size: 16px;
    padding: 0px;
    padding-top: 50px;
    z-index: 1;
    background-color: rgb(0, 0, 0);
    color: aliceblue;
    text-transform: uppercase;
    margin: 0;
    left: -150%;
    transition: all 0.3s ease-in-out;
  }
  .active {
    left: 0px;
  }
  .menu {
    display: block;
    transition: all 0.3s ease;
    &:hover {
      background-color: rgba(161, 151, 151, 0.644);
    }
    &:active {
      background-color: rgba(124, 117, 117, 0.644);
    }
  }
}
