.wrapper {
  width: 90%;
  padding-left: 35px;
  button,
  select {
    margin-bottom: 10px;
  }
}
.item {
  display: grid;
  grid-template-columns: 300px 1fr 30px 30px 30px;
  align-items: center;
  gap: 20px;
  border-bottom: 1px solid rgba(204, 204, 204, 0.336);

  p {
    margin: 0 0 10px 0;
  }
}
.progress {
  appearance: none;
  width: 40rem;
  max-width: 100%;
  &::-webkit-progress-bar {
    background-color: rgb(255, 255, 255);
    border-radius: 2px;
    box-shadow: 0 2px 5px rgba(233, 231, 231, 0.25) inset;
  }
}
.hidden {
  display: none;
}
.visible {
  display: block;
}
.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
@media screen and (max-width: 575px) {
  .item {
    display: grid;
    grid-template-columns: 200px 90px 25px 25px 25px;
  }
}
