.sort_block {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;
  margin: 40px;
}

@media screen and (max-width: 575px) {
  .sort_block {
    display: flex;
    width: 80%;
    height: 150px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 40px;
    margin-top: 80px;
  }
}
